<template>
  <div class="mdl-grid">
    <div class="mdl-cell mdl-cell--6-col mdl-cell--4-col-phone">
      <label class="mdl-radio mdl-js-radio mdl-js-ripple-effect" for="bank">
        <input type="radio" id="bank" class="mdl-radio__button" name="mode" value="Bank" v-model="transactionParam.mode" checked>
        <span class="mdl-radio__label">Bank</span>
      </label>
      <label class="mdl-radio mdl-js-radio mdl-js-ripple-effect" for="cash">
        <input type="radio" id="cash" class="mdl-radio__button" name="mode" value="Cash" v-model="transactionParam.mode">
        <span class="mdl-radio__label">Cash</span>
      </label>
      <label class="mdl-radio mdl-js-radio mdl-js-ripple-effect" for="cheque">
        <input type="radio" id="cheque" class="mdl-radio__button" name="mode" value="Cheque" v-model="transactionParam.mode">
        <span class="mdl-radio__label">Cheque</span>
      </label>
      <div v-if="transactionParam.mode==='Bank'">
        <button id="demo-menu-lower-left"
          class="mdl-button mdl-js-button">
          Select Banks
          <i class="material-icons">arrow_drop_down</i>
        </button>
        <ul class="mdl-menu mdl-menu--bottom-left mdl-js-menu mdl-js-ripple-effect"
          for="demo-menu-lower-left">
        <li class="mdl-menu__item"
          v-for="bank in banks"
          :key="bank.id"
          @click="bankParams=bank"
        >
          {{bank.name}}-{{bank.branch}}
        </li>
      </ul>

        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('account_holder_name')}">
            <input v-validate="'required|alpha_spaces|min:3'" name="account_holder_name" id="account_holder_name" class="mdl-textfield__input" v-model="transactionParam.account_holder_name">
            <label class="mdl-textfield__label" for="account_holder_name">Account Holder Name</label>
            <span class="mdl-textfield__error">{{ errors.first('account_holder_name') }}</span>
          </div>
        </div>
        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('account_number')}">
            <input v-validate="'required|integer|min:8'" name="account_number" id="account_number" class="mdl-textfield__input" v-model="transactionParam.account_number">
            <label class="mdl-textfield__label" for="account_number">Account Number</label>
            <span class="mdl-textfield__error">{{ errors.first('account_number') }}</span>
          </div>
        </div>
        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('bank_name')}">
            <input v-validate="'required|alpha_spaces|min:3'" name="bank_name" id="bank_name" class="mdl-textfield__input" v-model="transactionParam.bank_name">
            <label class="mdl-textfield__label" for="bank_name">Bank Name</label>
            <span class="mdl-textfield__error">{{ errors.first('bank_name') }}</span>
          </div>
        </div>
        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('branch')}">
            <input v-validate="'required|alpha_spaces|min:3'" name="branch" id="branch" class="mdl-textfield__input" v-model="transactionParam.branch">
            <label class="mdl-textfield__label" for="branch">Branch</label>
            <span class="mdl-textfield__error">{{ errors.first('branch') }}</span>
          </div>
        </div>
        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('city')}">
            <input v-validate="'required|alpha_spaces|min:3'" name="city" id="city" class="mdl-textfield__input" v-model="transactionParam.city">
            <label class="mdl-textfield__label" for="city">City</label>
            <span class="mdl-textfield__error">{{ errors.first('city') }}</span>
          </div>
        </div>
        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('ifsc_code')}">
            <input v-validate="'required|min:6'" name="ifsc_code" id="ifsc_code" class="mdl-textfield__input" v-model="transactionParam.ifsc_code">
            <label class="mdl-textfield__label" for="ifsc_code">IFSC code</label>
            <span class="mdl-textfield__error">{{ errors.first('ifsc_code') }}</span>
          </div>
        </div>
      </div>
      <div v-else-if="transactionParam.mode==='Cash'">
        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('account_holder_name')}">
            <input v-validate="'required|alpha_spaces|min:3'" name="account_holder_name" id="account_holder_name" class="mdl-textfield__input" v-model="transactionParam.account_holder_name">
            <label class="mdl-textfield__label" for="account_holder_name">Name</label>
            <span class="mdl-textfield__error">{{ errors.first('account_holder_name') }}</span>
          </div>
        </div>
      </div>
      <div v-else-if="transactionParam.mode==='Cheque'">
        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('account_holder_name')}">
            <input v-validate="'required|alpha_spaces|min:3'" name="account_holder_name" id="account_holder_name" class="mdl-textfield__input" v-model="transactionParam.account_holder_name">
            <label class="mdl-textfield__label" for="account_holder_name">Name</label>
            <span class="mdl-textfield__error">{{ errors.first('account_holder_name') }}</span>
          </div>
        </div>
        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('cheque')}">
            <input v-validate="'required|min:8'" name="cheque" id="cheque" class="mdl-textfield__input" v-model="transactionParam.cheque">
            <label class="mdl-textfield__label" for="cheque">Cheque Number</label>
            <span class="mdl-textfield__error">{{ errors.first('cheque') }}</span>
          </div>
        </div>
        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('bank_name')}">
            <input v-validate="'required|alpha_spaces|min:3'" name="bank_name" id="bank_name" class="mdl-textfield__input" v-model="transactionParam.bank_name">
            <label class="mdl-textfield__label" for="bank_name">Bank Name</label>
            <span class="mdl-textfield__error">{{ errors.first('bank_name') }}</span>
          </div>
        </div>
        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('branch')}">
            <input v-validate="'required|alpha_spaces|min:3'" name="branch" id="branch" class="mdl-textfield__input" v-model="transactionParam.branch">
            <label class="mdl-textfield__label" for="branch">Branch</label>
            <span class="mdl-textfield__error">{{ errors.first('branch') }}</span>
          </div>
        </div>
        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('city')}">
            <input v-validate="'required|alpha_spaces|min:3'" name="city" id="city" class="mdl-textfield__input" v-model="transactionParam.city">
            <label class="mdl-textfield__label" for="city">City</label>
            <span class="mdl-textfield__error">{{ errors.first('city') }}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="mdl-cell mdl-cell--6-col mdl-cell--4-col-phone">
      <div>
        <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('amount')}">
          <input v-validate="'required|decimal|min_value:1'" name="amount" id="amount" class="mdl-textfield__input" v-model="transactionParam.amount" @change="emitAmount">
          <label class="mdl-textfield__label" for="amount">Amount</label>
          <span class="mdl-textfield__error">{{ errors.first('amount') }}</span>
        </div>
        <div>
          <label class="mdl-radio mdl-js-radio mdl-js-ripple-effect" for="status.pending">
            <input type="radio" id="status.pending" class="mdl-radio__button" name="status" :value="status.pending" v-model="transactionParam.status">
            <span class="mdl-radio__label">{{status.pending}}</span>
          </label>
          <label class="mdl-radio mdl-js-radio mdl-js-ripple-effect" for="status.complete">
            <input type="radio" id="status.complete" class="mdl-radio__button" name="status" :value="status.complete" v-model="transactionParam.status">
            <span class="mdl-radio__label">{{status.complete}}</span>
          </label>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { ORDER_STATUS, TRANSACTION_MODE } from '@/constants'
export default {
  name: 'transaction-view',
  props: {
    transaction: {
      type: Object
    },
    client: {
      type: Object,
      default: []
    }
  },
  watch: {
    transactionParam () {
      window.componentHandler.upgradeAllRegistered()
      this.$forceUpdate()
      this.emitTransactionParams()
    }
  },
  computed: {
    transactionParam: {
      get () {
        return this.transaction || this.params
      }
    },
    banks () {
      return this.client.banks || []
    },
    bankParams: {
      get () {
        return this.transactionParam
      },
      set (bank) {
        this.transactionParam.bank_name = bank.name
        this.transactionParam.account_holder_name = bank.account_holder_name
        this.transactionParam.account_number = bank.account_number
        this.transactionParam.branch = bank.branch
        this.transactionParam.ifsc_code = bank.ifsc_code
        this.transactionParam.city = bank.city
        this.emitTransactionParams()
      }
    }
  },
  methods: {
    emitTransactionParams () {
      this.$emit('selectedTransaction', [ this.transactionParam ])
    },
    emitAmount () {
      this.$emit('amountEntered', this.transactionParam.amount)
      this.emitTransactionParams()
    },
    valid () {
      return this.$validator.validateAll()
    }
  },
  data () {
    return {
      params: {
        bank_name: '',
        account_holder_name: this.client.name,
        account_number: '',
        branch: '',
        city: '',
        amount: 0,
        ifsc_code: '',
        cheque: '',
        mode: TRANSACTION_MODE.bank,
        status: ORDER_STATUS.complete
      },
      status: ORDER_STATUS,
      mode: TRANSACTION_MODE
    }
  }
}
</script>



